.inputBlock{
    height: 30vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    float: left;
    background: transparent;
    border: 0;
    margin: 0;
    padding: 0;
    vertical-align: baseline;
    outline: none;
    box-sizing: border-box;
}

.input{
    width: 100%;
    background-color: #6a655d;
    font-style: italic;
    border-radius: 0;
    font-size: 13px;
    min-width: auto;
    letter-spacing: 1.4px;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
    font-family: "Poppins";
    line-height: 1.6;
    padding: 10px 15px!important;
    height: 44px;
    vertical-align: middle;
    border: 1px solid black;
    transition: all .3s ease;
    outline: none;
}
.input:hover {
    border: 1px solid #dcdcdc;
    background-color: #4b4643;
}
.input:focus{
    border: 2px solid #edb021;
    color: #dcdcdc;
}
.input::placeholder {
    color: #dcdcdc;
}

.textarea{
    outline: none;
    width: 100%;
    height: 150px;
    background-color: #6a655d;
    border-radius: 0;
    font-size: 13px;
    resize: vertical;
    font-family: "Poppins";
    letter-spacing: 1.2px!important;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
    line-height: 1.4;
    padding: 10px!important;
    width: 100%;
    border: 1px solid #1c222a;
    transition: all .3s ease;

}

.label{
    color:#dcdcdc;
}

.button{
    display: flex;
    justify-content: space-around
}

.textarea:hover {
    border: 1px solid #dcdcdc;
    background-color: #4b4643;
}

.textarea:focus{
    border: 2px solid #edb021;
    color: #dcdcdc;
}

.textarea::placeholder {
    color: #dcdcdc;
}

@media (min-width:  1000px) {
    .inputBlock{
        width: 90%
    }
}
