.work {
  width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 35px 20px;
  z-index: 10;
  position: relative;
  bottom:0;
  box-shadow: 0 15px 40px #0a0a0a;
  border-radius: 0 0 0 0;
  transition: all 0.4s ease-in-out;
  .picture {
    width: 100%;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
    position: relative;
    max-width: 500px;
    max-height: 400px;
    border-radius: 0 0 0 0;
    bottom:0;
    .button {
      background-color: #edb021;
      border: none;
      cursor: pointer;
      width: 150px;
      height: 20px;
      border-radius: 3px;
      font-family: 'Poppins', sans-serif;
      font-size: 14px;
      font-weight:bolder;
      color: #1c222a;
      position: relative;
      z-index: 1;
      text-align: center;
      line-height: 250%;
      opacity: 0;
    }
  }

  .picture::before {
    position: absolute;
    content: '';
    background-color: black;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    opacity: 0;
  }
  .title {
    padding-top: 10px;
    margin: 0;
    color: #1c222a;
  }
  .description {
    font-weight: 400;

    font-size: 13px;
    color: #1c222a;
  }
  .box {
    min-width: 500px ;
    min-height: 80px ;
    bottom:0;
    position: absolute;
    z-index: -1;
    background-color: #edb021;
    transition: all 0.4s ease-in-out;
  }
}

.work:hover{
   bottom:50px;
   z-index: 1;
   border-radius: 10px 10px 0 0;

  .box {
    bottom: -75px;
    display: block;
    z-index: -1;
    background-color: #edb021;
    border-radius: 0 0 10px 10px;
  }

  .picture{
    border-radius: 10px 10px 0 0;
  }
  .picture::before {
    position: absolute;
    content: '';
    background-color: black;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    opacity: 0.7;
    border-radius: 10px 10px 0 0;
    transition: all 0.4s ease-in-out;
  }
  .picture .button{
    display: block;
    opacity: 1;
    transition: all 0.4s ease-in-out;
    width: 180px;
    height: 40px;
  }
}
@media (max-width:  700px) {
  .work{
    margin: 45px 50px;
    max-width: 300px;
    min-width: 250px;
    width:100%;
    .box {
      min-width: 100px ;
      min-height: 300px ;
    }

  }
  .work:hover {
    bottom: 70px;
    z-index: 1;
    transition: all 0.4s ease-in-out;
    border-radius: 10px 10px 0 0;

    .box {
      min-width: 100px ;
      min-height: 110px;
      bottom: -110px;
      display: block;
      z-index: -1;
      transition: all 0.4s ease-in-out;
      background-color: #edb021;
      border-radius: 0 0 10px 10px;
    }
  }
}
